.bounding-box{
	position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: flex;
   
    flex-wrap: wrap;

    justify-content: center;
    cursor: pointer;
}
.frame {
width: auto;
height: auto;
border: 3px solid #ccc;
background: #eee; 
margin: auto;
padding: 15px 25px;
}
img {
width: 100%;
height: 100%;
}